import React, { useEffect, useState } from 'react'
import store from 'store2'

const LOCAL_KEY = 'dark-mode'

export const DarkMode = () => {
  const [isDark, setDark] = useState(store.get(LOCAL_KEY))

  useEffect(() => {
    const root = document.getElementsByTagName( 'html' )[0]
    if (isDark) {
      root.setAttribute( 'class', 'dark' )
    } else {
      root.setAttribute( 'class', '' )
    }
  }, [isDark])

  const handleChange = (e) => {
    setDark(e.target.checked)
    store.set(LOCAL_KEY, e.target.checked)
  }

  return (
    <fieldset class="form-group dark-mode-switch">
      <label class="paper-switch-2">
        <input
          id='paperSwitch10'
          name="paperSwitch10"
          type="checkbox"
          checked={isDark}
          onChange={handleChange}
        />
        <span class="paper-switch-slider round"></span>
      </label>
      <label for="paperSwitch10" class="paper-switch-2-label text-label">
        Dark
      </label>
    </fieldset>
  )
}
